const state = {
  products: [],
  tableNum: 0
};

const getters = {
  goodsList: state => {
    return state.products
  },
  getTableNum: state => {
    return state.tableNum
  }
};

const actions = {};
const mutations = {
  PUSH_TABLE_NUM(state, product) {
    state.tableNum = product
  },
  PUSH_ALL_PRODUCTS(state, product) {
    state.products = product
  }
};
export default {
  state,
  getters,
  actions,
  mutations
}



