import Vue from 'vue'
import Router from 'vue-router'
import home from "../components/home";
import goods from "../components/goods";
import order from "../components/order";
import cart from "../components/cart";
import goodsMassage from "../components/goodsMassage";
import getTableNum from "../components/getTableNum";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/getTableNum'
    },{
      path:'/getTableNum',
      component:getTableNum
    }, {
      path:'/home',
      component:home,
      children:[
        {
          path:'goods',
          component: goods
        }, {
          path:'order',
          component:order
        },{
          path:'cart',
          component:cart
        },
        {
          path:'goodsMessage',
          components:goodsMassage
        }
      ]
    }
  ]
})



