const state = {
  'goodsList': [],
  'key': 0,
};
const getters = {
  cartList: state => {
    return state.goodsList
  }
};
const actions = {
  clearGoods({commit}, product) {
    commit('DELETE_FROM_CART', product)
  },
  addNum({commit}, product) {
    commit('ADD_NUM', product)
  },
  reduceNum({commit}, product) {
    commit('REDUCE_NUM', product)
  }
};
const mutations = {
  ADD_TO_CART(state, product) {
    let record = state.goodsList.find(item => item.id == product.id);
    if (!record) {
      let key = state.key;
      state.goodsList[key] = product;
      state.goodsList[key].num = 1;
      state.key++;
    } else {
      record.num++
    }
    // console.info(state.goodsList)
  },
  DELETE_FROM_CART(state, product) {
    let index = state.goodsList.indexOf(product);
    state.goodsList.splice(index, 1);
    state.key -= 1;
    // console.info(state.goodsList)
  },
  ADD_NUM(state, product) {
    let record = state.goodsList.find(item => item.id == product.id);
    record.num++;
    // console.info(record)
  },
  REDUCE_NUM(state, product) {
    let record = state.goodsList.find(item => item.id == product.id);
    if (record.num === 1) {
      record.num = 1;
    }else {
      record.num--
    }
  }
};
export default {
  state,
  getters,
  actions,
  mutations
}



