
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import '../assets/css/iconfont/iconfont.css'
import {mapGetters, mapActions} from 'vuex'
import utils from '../axios/utils'

export default {
name: "cart",
components: {
Group,
Cell,
Flexbox,
FlexboxItem,
Divider,
Icon,
XButton,
Actionsheet,
Toast
},
data() {
return {
number: '1',
confirmOrder: false,
message: "点菜成功！",
confirmSuccess: false,
menus1: {
postCart: '<span style="color: red">确认菜单</span>',
}
}
},
watch: {},
computed: {
...mapGetters(['cartList']),
cost: function () {
let allCost = this.cartList, totalCost = 0, len = allCost.length;
for (let i = 0; i < len; i++) {
totalCost += allCost[i].num * allCost[i].price;
}
this.number++;
return totalCost;
}
},
methods: {
deleteGoods(item) {
this.clearGoods(item);
},
jiaNum(item) {
this.addNum(item);
this.number++
},
jianNum(item) {
this.reduceNum(item);
this.number--
},
...mapActions(['clearGoods']),
...mapActions(['addNum']),
...mapActions(['reduceNum']),
click(key) {
if (key === 'postCart') {

let order = [], len = this.cartList.length, cartToOrder = [];
for (let i = 0; i < len; i++) {
let orderProduct = {}, cartProduct = {};
orderProduct.name = this.cartList[i].name;
cartProduct.name = this.cartList[i].name;
cartProduct.price = this.cartList[i].price;
orderProduct.num = this.cartList[i].num;
cartProduct.num = this.cartList[i].num;
order[i] = orderProduct;
cartToOrder[i] = cartProduct;
}
let product = {};
product.totalPrice = this.cost;
order.push(product);
console.info(order);
console.info(cartToOrder);
let success = (response) => {
if (200 === response.data.code) {
this.confirmSuccess=true;
this.$store.commit('ADD_TO_ORDER', cartToOrder);
} else if (500 === response.data.code) {
alert(response.data.message)
}
};
utils.axiosMethod({
method: "POST",
url: "/api/order/cart/",
data: order,
callback: success
})
} else {
console.info(key)
}
},
choice() {
this.confirmOrder = true
},
}
}



