import Vuex from 'vuex'
import Vue from "vue";
import carts from "../store/carts"
import products from "../store/products"
import orders from "../store/orders"
Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {
    carts,
    products,
    orders
  },
});

export default store;




