
import XButton from 'vux/src/components/x-button/index.vue'

import utils from '../axios/utils'

export default {
name: "getTableNum",
components: {
XButton
},
methods: {
getTable() {
let success = (response) => {
if (200 === response.data.code) {//返回状态码

let getAllMessage = response.data.data;
let tableNum = getAllMessage.tableNum;
let product = getAllMessage.menu;
this.$store.commit('PUSH_TABLE_NUM', tableNum);
this.$store.commit('PUSH_ALL_PRODUCTS', product);
this.$router.push({path: '/home/goods'});
} else if (500 === response.data.code) {
alert(response.data.message)
} else {
alert("出错啦！");
}
};
utils.axiosMethod({
method: "POST",
url: "/api/table/request/",
data: true,
callback: success
})
}
}
}



