
import XTable from 'vux/src/components/x-table/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mapGetters} from 'vuex'
import utils from '../axios/utils'

export default {
name: "order",
components: {
XTable,
Flexbox,
FlexboxItem,
XButton
},
data() {
return {}
},
computed: {
...mapGetters(['orderList']),
cost: function () {
let allCost = this.orderList, totalCost = 0, len = allCost.length;
for (let i = 0; i < len; i++) {
totalCost += allCost[i].num * allCost[i].price;
}
this.number++;
return totalCost;
}
},
methods: {
choice(){
let tableNum = this.$store.getters.getTableNum;
let success = (response) => {
if (200 === response.data.code) {//返回状态码
alert(response.data.message);
} else if (500 === response.data.code) {
alert(response.data.message)
}
};
utils.axiosMethod({
method: "POST",
url: "/api/table/order/",
data:tableNum,
callback: success
})
}
}
}



