const state = {
  order: []
};

const getters = {
  orderList: state => {
    return state.order
  }
};

const actions = {};
const mutations = {
  ADD_TO_ORDER(state, product) {
    state.order = product
  }
};
export default {
  state,
  getters,
  actions,
  mutations
}



